<template>
  <div>
    <!--  -->
    <div class="search">
      <div class="left">
        <el-dropdown @command="handleCommand">
          <span class="el-dropdown-link">
            
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <!--  -->
            <el-dropdown-item command="all"></el-dropdown-item>
            <el-dropdown-item v-for="item in dropDowns" :command="item" :key="item">{{ item }}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <!-- tab -->
        <template v-if="clickSearch">
          <el-tag
            size="small"
            @click="search_allResume()"
            :effect="this.searchType === 'allResume' ? 'dark' : 'plain'"
          ></el-tag>
          <el-tag
            size="small"
            @click="search_newResume()"
            :effect="this.searchType === 'newResume' ? 'dark' : 'plain'"
          ></el-tag>
          <el-tag
            size="small"
            @click="search_beiXuan()"
            :effect="this.searchType === 'beiXuan' ? 'dark' : 'plain'"
          ></el-tag>
          <el-tag
            size="small"
            @click="search_quit()"
            :effect="this.searchType === 'quit' ? 'dark' : 'plain'"
          ></el-tag>
        </template>
        <template v-if="!clickSearch">
          <el-tag
            size="small"
            @click="search_allResume()"
            :effect="this.searchType === 'allResume' ? 'dark' : 'plain'"
          ></el-tag>
        </template>
      </div>
      <div class="right" style="display:flex">
        <el-input
          placeholder=""
          class="input-with-select"
          clearable
          v-model="searchForm.keyword"
          @clear="clearSearch"
        ></el-input>
        <el-button
          type="primary"
          slot="append"
          icon="el-icon-search"
          @click="searchKeyword(searchForm.keyword)"
        ></el-button>
      </div>
    </div>
    <!--  -->
    <el-card>
      <el-table :data="tableData" stripe style="width: 100%">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column prop="name" label=""></el-table-column>
        <el-table-column prop="gender" label=""></el-table-column>
        <el-table-column prop="eduDegree" label=""></el-table-column>
        <el-table-column prop="dirDesire" label=""></el-table-column>
        <el-table-column prop="graduation" label=""></el-table-column>
        <el-table-column prop="title" label=""></el-table-column>
        <el-table-column prop="stateName" label=""></el-table-column>
        <el-table-column label="" width="80">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              type="wraning"
              icon="el-icon-document-remove"
              size="mini"
              @click="DisplayResume(scope.row.applicationId)"
            ></el-button>
          </template>
        </el-table-column>
        <el-table-column>
          <template slot-scope="scope">
            <el-dropdown size="mini" split-button type="primary">
              
              <el-dropdown-menu>
                <el-dropdown-item @click.native="sendOffer(scope.row.applicationId)">offer</el-dropdown-item>
                <el-dropdown-item @click.native="quit(scope.row.applicationId)"></el-dropdown-item>
                <el-dropdown-item @click.native="interview(scope.row.applicationId)"></el-dropdown-item>
                <el-dropdown-item @click.native="onlineResume(scope.row)"></el-dropdown-item>
                <el-dropdown-item @click.native="downResume(scope.row)"></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        background
        layout="prev, pager, next"
        :total="config.total"
        :current-page.sync="config.page"
        :page-size="8"
        class="tablePagination"
        @current-change="getList()"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="ResumeVisible" width="40%">
      <!-- <span></span> -->
      <label for="ability"></label>
      <div class="resumeDetail">{{ this.ability}}</div>
      <label for="internship"></label>
      <div class="resumeDetail">{{ this.internship}}</div>
      <label for="workExperience"></label>
      <div class="resumeDetail">{{ this.workExperience}}</div>
      <label for="certificate"></label>
      <div class="resumeDetail">{{ this.certificate}}</div>
      <label for="jobDesire"></label>
      <div class="resumeDetail">{{ this.jobDesire}}</div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="ResumeVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogInterview" width="40%">
      <el-form
        :model="formIterview"
        :label-width="formIterviewLabelWidth"
        :rules="rulesInterview"
        ref="formIterview"
      >
        <el-form-item label=":" prop="description">
          <el-input
            v-model="formIterview.description"
            placeholder=":5198"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item label=":" :label-width="formIterviewLabelWidth" prop="rounds">
          <el-select v-model="formIterview.rounds" placeholder="">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="3"></el-option>
            <el-option label="" value="4"></el-option>
            <el-option label="" value="5"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogInterview = false"> </el-button>
        <el-button type="primary" @click="dialogInterviewConfirm"> </el-button>
      </div>
    </el-dialog>
    <!-- offer -->
    <el-dialog title="offer" :visible.sync="dialogSendoffer" width="40%">
      <el-form
        :model="formSendoffer"
        :label-width="formSendofferLabelWidth"
        :rules="rulesSendoffer"
        ref="formSendoffer"
      >
        <el-form-item label=":" prop="description">
          <el-input
            v-model="formSendoffer.description"
            placeholder=":offer"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogSendoffer = false"> </el-button>
        <el-button type="primary" @click="dialogSendofferConfirm"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// hr
import hrApi from "@/api/hr/hr";

export default {
  name: "Resume",
  data() {
    return {
      // 
      clickSearch: true,
      // 
      searchForm: {
        keyword: ""
      },
      // applicationId
      applicationId: 0,

      // offer
      dialogSendoffer: false,
      formSendofferLabelWidth: "120px",
      formSendoffer: { description: "" },
      rulesSendoffer: {
        description: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      // 
      dialogInterview: false,
      formIterviewLabelWidth: "120px",
      formIterview: { description: "", rounds: "" },
      /**/
      rulesInterview: {
        description: [
          { required: true, message: "", trigger: "blur" }
        ],
        rounds: [{ required: true, message: "", trigger: "blur" }]
      },
      // 5
      ability: "",
      internship: "",
      workExperience: "",
      certificate: "",
      jobDesire: "",
      // 
      searchType: "",
      // 
      dropDowns: [],
      // 
      ResumeVisible: false,
      // 
      tableData: [],
      // 
      config: {
        page: 1,
        total: 100,
        loading: false,
        lastPage: 1
      }
    };
  },
  methods: {
    // 
    getList() {
      this.config.loading = true;
      let params = {
        page: this.config.page,
        limit: 8
      };
      // 
      if (this.searchType === "allResume") {
        let url = "/resumeInfo";
        hrApi.getRequest1(url, params).then(res => {
          // 
          this.tableData = res.data.resumeInfo.list.map(item => {
            item.gender = item.gender === 1 ? "" : "";
            return item;
          });
          // 
          // this.lastPage = res.data.resumeInfo.lastPage;
        });
      } else if (this.searchType === "newResume") {
        let url = "/resume/0";
        hrApi.getRequest1(url, params).then(res => {
          // 
          this.tableData = res.data.resumes.list.map(item => {
            item.gender = item.gender === 1 ? "" : "";
            return item;
          });
          // 
          // this.lastPage = res.data.resumes.lastPage;
        });
      } else if (this.searchType === "beiXuan") {
        let url = "/resume/1";
        hrApi.getRequest(url).then(res => {
          this.tableData = res.data.resumes.list.map(item => {
            item.gender = item.gender === 1 ? "" : "";
            return item;
          });
        });
      } else if (this.searchType === "quit") {
        let url = "/resume/-1";
        hrApi.getRequest(url).then(res => {
          this.tableData = res.data.resumes.list.map(item => {
            item.gender = item.gender === 1 ? "" : "";
            return item;
          });
        });
      }
    },

    // 
    getPositionList() {
      let url = "/position/Combo";
      hrApi.getRequest(url).then(res => {
        // 
        this.dropDowns = res.data.titles;
      });
    },
    // 
    handleCommand(position) {
      // ,tab
      this.searchType = "allResume";
      this.clickSearch = true;
      let url = "/position/category";
      let params = {
        page: 1,
        limit: 8
      };
      let url1 = "/resumeInfo/";
      hrApi.getRequest3(url, position).then(res => {
        res;
        hrApi.getRequest2(url1, params).then(res => {
          this.tableData = res.data.resumeInfo.list.map(item => {
            item.gender = item.gender === 1 ? "" : "";
            return item;
          });
        });
      });

      this.$message("click on  " + position);
    },
    // 
    clearSearch() {
      this.clickSearch = true;
      this.getList();
    },
    // 
    searchKeyword(keyword) {
      this.clickSearch = false;
      let url = "/search";
      let params = "keyword=" + keyword;
      hrApi.postRequest(url, params).then(res => {
        this.tableData = res.data.posInfo.list.map(item => {
          item.gender = item.gender === 1 ? "" : "";
          return item;
        });
      });
    },
    // hroffer
    dialogSendofferConfirm() {
      this.dialogSendoffer = false;
      this.$refs.formSendoffer.validate(valid => {
        /*rules*/
        if (valid) {
          // url 
          let url = "/sendNews/" + this.applicationId;
          let params =
            "interviewsDesc=" + this.formSendoffer.description + "&state=" + -3;
          hrApi.putRequest1(url, params).then(res => {
            res;
            this.getList();
          });
          this.$message.success("offer!");
        } else {
          this.$message.error("offer!");
          return false;
        }
      });
    },
    // hr
    dialogInterviewConfirm() {
      this.dialogInterview = false;
      this.$refs.formIterview.validate(valid => {
        /*rules*/
        if (valid) {
          // url 
          let url = "/interview/" + this.applicationId;
          let params =
            "flag=" +
            this.formIterview.rounds +
            "&interviewsDesc=" +
            this.formIterview.description;
          hrApi.putRequest1(url, params).then(res => {
            if (res.statusText === "OK") {
              this.$message.success("!");

              this.getList();
            }
          });
          this.$refs.formIterview.resetFields();
        } else {
          this.$message.error("!!");
          return false;
        }
      });
    },
    //  
    search_allResume() {
      // 
      this.searchType = "allResume";
      this.getList();
    },
    search_newResume() {
      // 
      this.searchType = "newResume";
      this.getList();
    },
    search_beiXuan() {
      // 
      this.searchType = "beiXuan";
      this.getList();
    },
    search_quit() {
      // 
      this.searchType = "quit";
      this.getList();
    },

    // 

    // offer
    sendOffer(applicationId) {
      this.dialogSendoffer = true;
      this.applicationId = applicationId;
    },
    // 
    quit(id) {
      let url = "/removeresume/" + id;
      hrApi.putRequest(url).then(res => {
        res;
      });
    },
    // 
    interview(applicationId) {
      this.dialogInterview = true;
      this.applicationId = applicationId;
    },
    // 
    onlineResume(data) {
      let url = "/hr/resume/download/" + data.userId + "/inline";
      this.$http.get(url).then(res => {
        // 
        console.log(res);
        window.location.href = url;
        // this.$router.push(res.config.url);
      });
    },
    // 
    downResume(data) {
      let url = "/hr/resume/download/" + data.userId + "/download";
      this.$http.get(url).then(res => {
        // 
        res;
        window.location.href = url;
      });
    },

    // 
    DisplayResume(applicationId) {
      let url = "/resume/ResumeDesc";
      let parms = "applicationId=" + applicationId;
      hrApi.postRequest(url, parms).then(res => {
        let resumeEntity = res.data.resumeEntity;
        this.ability = resumeEntity.ability;
        this.internship = resumeEntity.internship;
        this.workExperience = resumeEntity.workExperience;
        this.certificate = resumeEntity.certificate;
        this.jobDesire = resumeEntity.jobDesire;
      });

      this.ResumeVisible = true;
    }
  },
  created() {
    //  
    this.searchType = "allResume";
    // 
    this.getList();
    // 
    this.getPositionList();
  }
};
</script>

<style lang="scss" scoped>
// div
.search {
  display: flex;
  justify-content: space-between;
  margin-left: 10px;
  margin-bottom: 10px;
  .el-tag {
    margin-left: 10px;
  }
}
// dialog
.el-dialog {
  .resumeDetail {
    height: 100px;
    border: 1px #0f0f0f solid;
    border-radius: 5px;
    margin-bottom: 15px;
    margin-top: 5px;
  }
}
.tablePagination {
  margin-top: 15px;
}
// 
.el-dropdown-link {
  cursor: pointer;
  color: #409eff;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.demonstration {
  display: block;
  color: #8492a6;
  font-size: 14px;
  margin-bottom: 20px;
}
</style>
