<template>
  <!-- RootHR -->
  <div class="manage">
    <!--  -->
    <el-dialog
      :visible.sync="addDialogVisible"
      :title="dialogType=='add' ? 'HR':'HR'"
      class="addDialog"
      @close="closeAddDialog"
    >
      <!--  -->
      <el-form
        ref="operateFormRef"
        :model="operateForm"
        :rules="operateRules"
        label-width="80px"
        class="operateForm"
      >
        <el-form-item
          v-for="item in operateFormLabel"
          :key="item.model"
          :label="item.label"
          :prop="item.model"
        >
          <!--  -->
          <el-date-picker
            v-if="item.type == 'date'"
            v-model="operateForm[item.model]"
            type="date"
            placeholder=""
            value-format="timestamp"
          ></el-date-picker>

          <!--  -->
          <el-input
            v-else
            v-model="operateForm[item.model]"
            :type="item.inputType"
            :disabled="item.readonly"
          ></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="confirmAddDialog"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <div class="manage-header">
      <el-row type="flex" justify="space-between" style="width: 100%">
        <!--  -->
        <el-col :span="6">
          <el-button icon="el-icon-plus" type="primary" @click="addHR">HR</el-button>
        </el-col>
        <!--  -->
        <el-col :span="10">
          <el-input
            placeholder=""
            v-model="searchForm.keyword"
            class="input-with-select"
            clearable
            @clear="getList"
          >
            <el-button type="primary" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
    </div>

    <!--  -->
    <el-card>
      <!--  -->
      <el-table :data="tableData" stripe style="width: 100%">
        <!--  -->
        <el-table-column type="index"></el-table-column>
        <!--  -->
        <el-table-column
          v-for="item in tableLabel"
          :key="item.prop"
          :prop="item.prop"
          :label="item.label"
          :width="item.width ? item.width : 80"
        ></el-table-column>
        <!--  -->
        <el-table-column label="">
          <template slot-scope="slot">
            <el-tooltip class="item" effect="dark" content="" placement="top-start">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="editHR(slot.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top-start">
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="deleteHR(slot.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        background
        layout="prev, pager, next"
        :total="config.total"
        :current-page.sync="config.page"
        :page-size="config.limit"
        class="tablePagination"
        @current-change="getList()"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // HRID
      markHRID: 0,
      // 
      dialogType: "add",
      // HRdialog
      addDialogVisible: false,
      operateForm: {
        name: "",
        hrID: "",
        mobile: "",
        email: "",
        description: "",
        password: "",
        departmentId: ""
      },
      operateFormLabel: [
        {
          model: "name",
          label: ""
        },
        {
          model: "hrID",
          label: "hrID",
          readonly: true
        },
        {
          model: "mobile",
          label: ""
        },
        {
          model: "email",
          label: ""
        },
        {
          model: "description",
          label: ""
        },
        {
          model: "departmentId",
          label: ""
        },
        {
          model: "password",
          label: ""
        }
      ],
      // 
      operateRules: {
        name: [{ required: true, message: "HR", trigger: "blur" }],
        hrID: [{ required: true, message: "HRID", trigger: "blur" }],
        mobile: [
          { required: true, message: "HR", trigger: "blur" }
        ],
        email: [{ required: true, message: "HR", trigger: "blur" }],
        description: [
          { required: true, message: "HR", trigger: "blur" }
        ],
        departmentId: [
          { required: true, message: "HR", trigger: "blur" }
        ],
        password: [
          { required: true, message: "HR", trigger: "blur" }
        ]
      },
      // 
      tableData: [
        {
          name: "Piggy",
          hrID: 11,
          mobile: "15819091730",
          email: "piggy1024@qq.com",
          description: "HR",
          departmentId: 0
        }
      ],
      // 
      tableLabel: [
        {
          prop: "name",
          label: "",
          width: 130
        },
        {
          prop: "hrID",
          label: "hrID",
          width: 120
        },
        {
          prop: "mobile",
          label: "",
          width: 200
        },
        {
          prop: "email",
          label: "",
          width: 250
        },
        {
          prop: "description",
          label: "",
          width: 250
        },
        {
          prop: "departmentId",
          label: "",
          width: 120
        }
      ],
      // 
      config: {
        page: 1,
        limit: 8,
        total: 20,
        loading: false
      },
      formLabel: [
        {
          model: "keyword",
          label: ""
        }
      ],
      searchForm: {
        keyword: ""
      }
    };
  },
  methods: {
    closeAddDialog() {},
    selectChange() {},
    confirmAddDialog() {
      if (this.dialogType === "edit") {
        console.log("edit");
        let url =
          "hr/info/update/" +
          this.markHRID +
          "?hrid=" +
          this.operateForm.hrID +
          "&hrMobile=" +
          this.operateForm.mobile +
          "&hrPassword=" +
          this.operateForm.password +
          "&hrName=" +
          this.operateForm.name +
          "&hrEmail=" +
          this.operateForm.email +
          "&description=" +
          this.operateForm.description +
          "&departmentId=" +
          this.operateForm.departmentId;
        this.$http.post(url).then(res => {
          res;
          // console.log(res);
        });
      } else {
        // console.log("add");
      }
    },
    // HR
    addHR() {},
    // HR
    editHR(data) {
      this.dialogType = "edit";
      this.addDialogVisible = true;
      this.operateForm = data;
      // HRID   datascope.row
      this.markHRID = data.hrID;
    },
    // HR
    deleteHR(data) {
      // console.log(data);
      let url = "roothr/delete/" + data.hrID;
      this.$http.post(url).then(res => {
        res;
      });
    },
    // 
    getList() {
      let url = "/roothr/search?" + "page=" + this.config.page + "&limit=" + 8;
      this.$http.post(url).then(res => {
        console.log(res.data);
        this.tableData = res.data.posInfo.list;
      });
    }
  },
  created() {
    // 
    // this.getList();
  }
};
</script>

<style lang="scss" scoped>
.manage {
  min-width: 1250px;
}
.manage-header {
  margin-bottom: 15px;
}
.tablePagination {
  margin-top: 15px;
}
.operateForm {
  position: relative;

  .otherInput {
    position: absolute;
    top: 1px;
    left: 290px;
    width: 150px;
  }
}
</style>