<template>
  <div
    v-loading="loading"
    element-loading-text=""
    element-loading-spinner="el-icon-loading"
    element-loading-background="white"
    class="LoginContainer"
  >
    <div style="text-align:center">HR</div>
    <el-form :rules="rules" :model="LoginForm" ref="LoginForm">
      <!--refrefs-->
      <el-form-item prop="username">
        <!--prop-->
        
        <el-input
          size="normal"
          type="text"
          v-model="LoginForm.username"
          auto-complete="off"
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item prop="password">
        
        <el-input
          size="normal"
          type="password"
          v-model="LoginForm.password"
          auto-complete="off"
          placeholder=""
          @keydown.enter.native="submitLogin"
        >
          <!---->
        </el-input>
      </el-form-item>
      <el-checkbox v-model="checked" class="LoginRem"></el-checkbox>
      <div style="float:right">
        ?
        <router-link to="/hr_register"></router-link>
      </div>
      <el-button size="normal" type="primary" style="width: 100%" @click="submitLogin"></el-button>
    </el-form>
  </div>
</template>

<script>
import hrApi from "@/api/hr/hr";
export default {
  name: "Login",
  data() {
    return {
      loading: false,
      checked: true,
      LoginForm: {
        username: "",
        password: ""
      },
      /**/
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" }
        ],
        password: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  methods: {
    submitLogin() {
      this.$refs.LoginForm.validate(valid => {
        /*rules*/
        if (valid) {
          this.loading = true;
          // url 
          let url = "/loginPost";
          let params =
            "hrMobile=" +
            this.LoginForm.username +
            "&hrPass=" +
            this.LoginForm.password;
          hrApi
            .postRequest(url, params)
            .then(res => {
              this.loading = false;
              console.log(res);
              // res.data ===0    === -1   === 1 root
              if (res.data === 0) {
                this.$store.state.tab.isRoot = true;
                this.$message.success("");
                window.sessionStorage.setItem("user", JSON.stringify(res));
                this.$router.replace("/Hr_index");
              } else if (res.data === 1) {
                this.$store.state.tab.isRoot = true;
                this.$message.success("");
                window.sessionStorage.setItem("user", JSON.stringify(res));
                this.$router.replace("/Hr_index");
              } else {
                this.$message.error("!");
                this.$router.push("/");
              }
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          this.$message.error("");
          return false;
        }
      });
    }
  }
};
</script>

<style scoped>
.LoginContainer {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: white;
  border: 1px solid silver;
}
.LoginRem {
  margin-bottom: 20px;
}
</style>