<template>
  <!--  -->
  <div class="manage">
    <!--  -->
    <el-dialog
      :visible.sync="addDialogVisible"
      :title="dialogType=='add' ? '':''"
      class="addDialog"
      @close="closeAddDialog"
    >
      <!--  -->
      <el-form
        ref="operateFormRef"
        :model="operateForm"
        :rules="operateRules"
        label-width="80px"
        class="operateForm"
      >
        <el-form-item
          v-for="item in operateFormLabel"
          :key="item.model"
          :label="item.label"
          :prop="item.model"
        >
          <!--  -->
          <el-date-picker
            v-if="item.type == 'date'"
            v-model="operateForm[item.model]"
            type="date"
            placeholder=""
            value-format="timestamp"
          ></el-date-picker>
          <!--  -->
          <el-select
            v-else-if="item.type == 'select'"
            v-model="operateForm[item.model]"
            placeholder=""
            @change="selectChange"
          >
            <el-option
              v-for="each in item.options"
              :key="each.value"
              :label="each.label"
              :value="each.value"
            ></el-option>
          </el-select>
          <!--  -->
          <el-input v-else v-model="operateForm[item.model]" :type="item.inputType"></el-input>
        </el-form-item>
        <!--  -->
        <el-input
          class="otherInput"
          :style="operateForm.categoryId == -1 ? 'display:block' : 'display: none'"
          autofocus
        ></el-input>
      </el-form>
      <!--  -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="confirmAddDialog"> </el-button>
      </div>
    </el-dialog>

    <!--  -->
    <div class="manage-header">
      <el-row type="flex" justify="space-between" style="width: 100%">
        <!--  -->
        <el-col :span="6">
          <el-button icon="el-icon-plus" type="primary" @click="addJob"></el-button>
        </el-col>
        <!--  -->
        <el-col :span="10">
          <el-input
            placeholder=""
            v-model="searchForm.keyword"
            class="input-with-select"
            clearable
            @clear="getList"
          >
            <el-button type="primary" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>
    </div>

    <!--  -->
    <el-card>
      <!--  -->
      <el-table :data="tableData" stripe style="width: 100%">
        <!--  -->
        <el-table-column type="index"></el-table-column>
        <!--  -->
        <el-table-column
          v-for="item in tableLabel"
          :key="item.prop"
          :prop="item.prop"
          :label="item.label"
          :width="item.width ? item.width : 80"
        ></el-table-column>
        <!--  -->
        <el-table-column label="">
          <template slot-scope="slot">
            <el-tooltip class="item" effect="dark" content="" placement="top-start">
              <el-button
                type="success"
                icon="el-icon-view"
                size="mini"
                @click="toDetails(slot.row)"
              ></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top-start">
              <el-button type="primary" icon="el-icon-edit" size="mini" @click="editJob(slot.row)"></el-button>
            </el-tooltip>
            <el-tooltip class="item" effect="dark" content="" placement="top-start">
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="deleteJob(slot.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        background
        layout="prev, pager, next"
        :total="config.total"
        :current-page.sync="config.page"
        :page-size="config.limit"
        class="tablePagination"
        @current-change="getList()"
      ></el-pagination>
    </el-card>
  </div>
</template>
<script>
// import hrApi from "@/api/hr/hr";

export default {
  data() {
    return {
      dialogType: "add",
      // dialog
      addDialogVisible: false,
      operateForm: {
        title: "",
        requirement: "",
        quantity: "",
        salaryDown: 0,
        salaryUp: 0,
        workCity: "",
        validDate: null,
        departmentId: "",
        hrIdPub: "",
        statePub: 1,
        categoryId: ""
      },
      operateFormLabel: [
        {
          model: "categoryId",
          label: "",
          options: [],
          type: "select"
        },
        {
          model: "title",
          label: ""
        },
        {
          model: "requirement",
          label: "",
          inputType: "textarea"
        },
        {
          model: "quantity",
          label: "",
          inputType: "number"
        },
        {
          model: "workCity",
          label: ""
        },
        {
          model: "salaryDown",
          label: "",
          inputType: "number"
        },
        {
          model: "salaryUp",
          label: "",
          inputType: "number"
        },
        {
          model: "validDate",
          label: "",
          type: "date"
        }
      ],
      // 
      operateRules: {
        categoryId: [
          { required: true, message: "", trigger: "blur" }
        ],
        title: [{ required: true, message: "", trigger: "blur" }],
        requirement: [
          { required: true, message: "", trigger: "blur" }
        ],
        quantity: [
          { required: true, message: "", trigger: "blur" }
        ],
        workCity: [
          { required: true, message: "", trigger: "blur" }
        ],
        validDate: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      // 
      tableData: [],
      // 
      tableLabel: [
        {
          prop: "title",
          label: "",
          width: 130
        },
        {
          prop: "quantity",
          label: ""
        },
        {
          prop: "hits",
          label: ""
        },
        {
          prop: "workCity",
          label: ""
        },
        {
          prop: "requirement",
          label: "",
          width: 250
        },
        {
          prop: "money",
          label: "",
          width: 120
        },
        {
          prop: "releaseDate",
          label: "",
          width: 120
        },
        {
          prop: "validDate",
          label: "",
          width: 120
        }
      ],
      // 
      config: {
        page: 1,
        limit: 8,
        total: 20,
        loading: false
      },
      formLabel: [
        {
          model: "keyword",
          label: ""
        }
      ],
      searchForm: {
        keyword: ""
      },
      // hr
      hrInfo: {}
    };
  },
  methods: {
    // 
    async getList() {
      this.config.loading = true;
      const res = await this.$http.get("/hr/position/" + this.config.page, {
        params: { limit: this.config.limit }
      });
      if (res.status == 200) {
        this.tableData = res.data.positions.list;
        this.tableData.forEach(item => {
          item.releaseDate = this.getDate(item.releaseDate.time);
          if (item.validDate) {
            item.validDate = this.getDate(item.validDate.time);
          }
          item.money = item.salaryDown + "~" + item.salaryUp;
        });
        this.config.total = res.data.positions.total;
        this.hrInfo = res.data.hr;
        // console.log(res);
        this.config.loading = false;
      } else {
        this.$message.error("");
      }
    },
    // 
    toDetails(row) {
      this.$message.info("");
      // console.log(row);
      window.open("position/" + row.positionId, "_blank");
    },
    // 
    async editJob(row) {
      this.dialogType = "edit";
      this.addDialogVisible = true;
      Object.assign(this.operateForm, row);
      // console.log("operateForm", this.operateForm)
      this.operateForm.positionId = row.positionId;
      this.operateForm.validDate = null;
    },
    // 
    deleteJob(row) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          let id = row.positionId;
          const res = await this.$http.post("/position" + id + "/delete");
          if (res.status == 200) {
            this.getList();
            this.$message.success("");
          } else {
            this.$message.error("");
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    // 
    addJob() {
      this.dialogType = "add";
      this.operateForm.departmentId = this.hrInfo.departmentId;
      this.operateForm.hrIdPub = this.hrInfo.hrId;
      this.operateForm.statePub = 1;
      this.operateForm.validDate = null;
      this.addDialogVisible = true;
    },
    // 
    selectChange() {},
    // 
    confirmAddDialog() {
      this.operateForm.salaryDown = parseInt(this.operateForm.salaryDown);
      this.operateForm.salaryUp = parseInt(this.operateForm.salaryUp);

      this.$refs.operateFormRef.validate(async valid => {
        if (valid) {
          var res;
          // 
          if (this.dialogType == "add") {
            var url = "/hr/position/create?";
            for (var key in this.operateForm) {
              url += key + "=" + this.operateForm[key] + "&";
            }
            url = url.slice(0, -1);
            res = await this.$http.post(url);
            // console.log(res)
          } else if (this.dialogType == "edit") {
            // 
            url =
              "/position" +
              this.operateForm.positionId +
              "/update/?title=" +
              this.operateForm.title +
              "&requirement=" +
              this.operateForm.requirement +
              "&quantity=" +
              this.operateForm.quantity +
              "&workCity=" +
              this.operateForm.workCity +
              "&salaryDown=" +
              this.operateForm.salaryDown +
              "&salaryUp=" +
              this.operateForm.salaryUp +
              "&validDate=" +
              this.operateForm.validDate +
              "&categoryId=" +
              this.operateForm.categoryId;
            res = await this.$http.post(url);
            // console.log(res)
          }
          if (res.status == 200) {
            this.addDialogVisible = false;
            this.$message.success("");
            this.getList();
          } else {
            this.$message.error("");
          }
        } else {
          this.$message.info("");
        }
      });
    },
    // 
    closeAddDialog() {
      this.$refs.operateFormRef.resetFields();
    },

    // 
    getDate(time) {
      if (time) {
        var date = new Date(time);
        var year = date.getFullYear();
        var month = date.getMonth() + 1;
        if (month < 10) {
          month = "0" + month;
        }
        var day = date.getDate();
        if (day < 10) {
          day = "0" + day;
        }
        return year + "/" + month + "/" + day;
      }
      return null;
    },

    // 
    async getCategory() {
      const res = await this.$http.get("/hr/getCategory");
      if (res.status == 200) {
        res.data.categoryEntities.forEach(item => {
          var obj = {};
          obj.value = item.categoryId;
          obj.label = item.categoryName;
          this.operateFormLabel[0].options.push(obj);
        });
        var obj = {};
        obj.value = -1;
        obj.label = "";
        this.operateFormLabel[0].options.push(obj);
      }
      // console.log(res);
    }
  },
  created() {
    this.getList();
    this.getCategory();
  }
};
</script>
<style lang="scss" scoped>
.manage {
  min-width: 1250px;
}
.manage-header {
  margin-bottom: 15px;
}
.tablePagination {
  margin-top: 15px;
}
.operateForm {
  position: relative;

  .otherInput {
    position: absolute;
    top: 1px;
    left: 290px;
    width: 150px;
  }
}
</style>
