<template>
  <div>
    <el-card shadow="hover">
      <el-row :gutter="20">
        <el-col :span="8" class="oo">
          <div class="user">
            <img :src="userImg" />
          </div>
          <div>
            <div>
              <p class="name">
                
                {{ this.userlist.hrName }}
              </p>
              <!-- <p class="access">{{ this.userlist.access }}</p> -->
            </div>
          </div>
        </el-col>
        <el-col :span="12">
          <div class="userinfo">
            <p class="title"></p>
            <p>
              ID
              {{ this.userlist.hrId }}
            </p>
            <p>
              
              {{ this.userlist.hrMobile }}
            </p>
            <p>
              
              {{ this.userlist.hrEmail }}
            </p>
            <p>
              
              {{ this.userlist.description }}
            </p>
          </div>
        </el-col>
        <el-col :span="4">
          <!--  showEditDialog()ID  showEditDialog(id)-->
          <el-button type="primary" @click="showEditDialog()"></el-button>
        </el-col>
      </el-row>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%">
      <el-form :model="editForm" ref="editFormRef" label-width="70px">
        <el-form-item label="ID">
          <!-- ID -->
          <el-input v-model="editForm.hrId" disabled></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.hrName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.hrEmail"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.hrMobile"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editForm.description"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.trim="editForm.hrPassword" type="password"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import hrApi from "@/api/hr/hr";
export default {
  data() {
    return {
      userImg: require("../../assets/images/user.png"),
      userlist: {
        hrName: "Nick",
        access: "HR",
        hrId: "",
        hrMobile: "",
        hrEmail: "",
        description: "",
        password: ""
      },
      // 
      editDialogVisible: false,
      // 
      editForm: {
        hrId: "",
        hrName: "",
        hrEmail: "",
        hrMobile: "",
        description: "",
        hrPassword: ""
      }
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    getUserList() {
      hrApi.postRequest1("/info").then(res => {
        // console.log(res);

        this.userlist = res.data.hr;
        // dialog
        this.editForm = this.userlist;
        // console.log(res.data.hr);
      });
    },
    // 
    showEditDialog() {
      this.editDialogVisible = true;
    },
    editUserInfo() {
      let url = "/info/update/" + this.userlist.hrId;
      // post
      let params =
        "hrMobile=" +
        this.editForm.hrMobile +
        "&hrPassword=" +
        this.editForm.hrPassword +
        "&hrName=" +
        this.editForm.hrName +
        "&hrEmail=" +
        this.editForm.hrEmail +
        "&description=" +
        this.editForm.description +
        "&departmentId=" +
        this.editForm.departmentId;

      hrApi.postRequest(url, params).then(res => {
        // console.log(res);
        if (res.status === 200) {
          this.$message.success("");
          // 
          this.getUserList();
        }
      });
      // 
      // this.$refs.editFoemRef.validata(async vaild => {
      //   if (!vaild) return;
      //   // 
      //   const { data: res } = await this.$http.get('users/' + this.editForm.id, {
      //     email: this.editForm.email,
      //     mobile: this.editForm.mobile
      //   })
      //   if (res.meta.status !== 200) {
      //     return this.$message.error('');
      //   } else {
      //     // 
      //     this.editDialogVisible = false;
      //     // 
      //     this.getUserList();
      //     // 
      //     return this.$message.success('');
      //   }
      // })
      this.editDialogVisible = false;
    }
  }
};
</script>
<style scoped>
.oo {
  text-align: center;
}
.user {
  padding-bottom: 20px;
  margin-bottom: 20px;
}
.user img {
  text-align: center;
  height: 150px;
  width: 150px;
  border-radius: 50%;
  vertical-align: middle;
}
.name {
  font-size: 16px;
  margin-bottom: 10px;
  text-align: center;
}

.access {
  color: #999;
  text-align: center;
}
.title {
  font-size: 20px;
  color: black;
  padding-bottom: 10px;
}
.userinfo p {
  padding-bottom: 15px;
}
</style>
