<template>
  <div>
    <el-container style="height:100%">
      <el-aside style="width: 200px">
        <el-menu router background-color="#545c64" text-color="#fff" default-active="/Person_Info">
          <div class="title">Hr</div>
          <el-menu-item
            :index="item.path"
            v-for="item in menu"
            :key="item.path"
            @click="clickMenu(item)"
          >
            <i :class="'el-icon-' + item.icon"></i>
            <span slot="title">{{ item.label }}</span>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-container>
        <el-header class="homeHeader">
          <div>
            <!-- //  -->
            <!-- <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
              <el-breadcrumb-item></el-breadcrumb-item>
              <el-breadcrumb-item></el-breadcrumb-item>
              <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>-->
          </div>
          <div>
            <el-button
              icon="el-icon-setting"
              type="text"
              style="margin-right: 8px;color: #fff;"
              size="normal"
            ></el-button>
            <el-dropdown class="userInfo" @command="commandHandler">
              <span class="el-dropdown-link">
                
                <!--v-bind-->
              </span>
              <el-dropdown-menu slot="dropdown">
                <!-- <el-dropdown-item command=""></el-dropdown-item>
                <el-dropdown-item command=""></el-dropdown-item>-->
                <el-dropdown-item command="logout" divided></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-header>
        <!-- <common-tab></common-tab> -->
        <div class="tabs">
          <el-tag
            :key="tag.name"
            v-for="(tag, index) in tags"
            :closable="tag.name !== 'person_info'"
            :disable-transitions="false"
            @close="handleClose(tag, index)"
            size="medium"
            @click="changeMenu(tag)"
            type="primary"
            :effect="$route.name === tag.name ? 'dark' : 'plain'"
          >{{ tag.label }}</el-tag>
        </div>

        <el-main style="height:700px">
          <router-view v-if="$route.meta.keepAlive" />
        </el-main>
      </el-container>
    </el-container>

    <!-- <div class="line"></div> -->
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import hrApi from "@/api/hr/hr";

export default {
  name: "Hr_Index",
  components: {},
  data() {
    return {
      activeIndex: "4",
      user: JSON.parse(window.sessionStorage.getItem("user"))
    };
  },
  computed: {
    // store
    menu() {
      return this.$store.state.tab.menu;
    },
    ...mapState({
      tags: state => state.tab.tabsList
    })
  },
  created() {
    // (rootHRmenu)
    if (!this.$store.state.tab.isRoot) {
      // HR
      let menu = [
        {
          path: "/person_info",
          name: "person_info",
          label: "",
          icon: "s-home",
          url: "person_info"
        },
        {
          path: "/resume",
          name: "resume",
          label: "",
          icon: "discover",
          url: "resume"
        },
        {
          path: "/position",
          name: "position",
          label: "",
          icon: "user",
          url: "position"
        },
        {
          path: "/interview",
          name: "interview",
          label: "",
          icon: "s-check",
          url: "interview"
        },
        {
          path: "/employ",
          name: "employ",
          label: "",
          icon: "aim",
          url: "employ"
        },
        {
          path: "/talent",
          name: "talent",
          label: "",
          icon: "s-custom",
          url: "talent"
        }
      ];
      this.$store.commit("handleMenu", menu);
    } else {
      // ROOTHR(HR)
      let menu = [
        {
          path: "/person_info",
          name: "person_info",
          label: "",
          icon: "s-home",
          url: "person_info"
        },
        {
          path: "/root",
          name: "root",
          label: "HR",
          icon: "setting",
          url: "root"
        },
        {
          path: "/resume",
          name: "resume",
          label: "",
          icon: "discover",
          url: "resume"
        },
        {
          path: "/position",
          name: "position",
          label: "",
          icon: "user",
          url: "position"
        },
        {
          path: "/interview",
          name: "interview",
          label: "",
          icon: "s-check",
          url: "interview"
        },
        {
          path: "/employ",
          name: "employ",
          label: "",
          icon: "aim",
          url: "employ"
        },
        {
          path: "/talent",
          name: "talent",
          label: "",
          icon: "s-custom",
          url: "talent"
        }
      ];
      this.$store.commit("handleMenu", menu);
    }

    //sessionStorage
    if (sessionStorage.getItem("store")) {
      this.$store.replaceState(
        Object.assign(
          {},
          this.$store.state,
          JSON.parse(sessionStorage.getItem("store"))
        )
      );
    }

    //vuexsessionStorage
    window.addEventListener("beforeunload", () => {
      sessionStorage.setItem("store", JSON.stringify(this.$store.state));
    });

    // 
    this.$router.push("/Person_Info");
    // tab
    this.currentMenu = [];
  },
  methods: {
    // tab
    changeMenu(item) {
      this.$router.push({ name: item.name });
      this.$store.commit("selectMenu", item);
    },

    clickMenu(item) {
      this.$router.push({ name: item.name });
      this.$store.commit("selectMenu", item);
    },

    ...mapMutations({
      close: "closeTab"
    }),

    // tab
    handleClose(tag, index) {
      let length = this.tags.length - 1;
      this.close(tag);

      // 
      if (tag.name !== this.$route.name) {
        return;
      }
      // 
      if (index === length) {
        this.$router.push({ name: this.tags[index - 1].name });
      } else {
        // 
        this.$router.push({ name: this.tags[index].name });
      }
    },

    // 
    commandHandler(cmd) {
      if (cmd == "logout") {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            hrApi.postRequest0("/logout").then(() => {
              window.sessionStorage.removeItem("user");
              // this.$store.commit("initRoutes", []);
              this.$router.replace("/");
              this.$message({
                type: "success",
                message: "!"
              });
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.homeHeader {
  background: #333;
  display: flex; /**/
  align-items: center;
  font-size: 30px;
  justify-content: space-between;
}
.title {
  font-family: ;
  color: white;
  font-size: 35px;
  text-align: center;
  margin: 15px 0;
}
.el-dropdown-link {
  display: flex;
  align-items: center;
  color: #f2fcff;
}
.el-dropdown-link img {
  width: 48px;
  height: 48px;
  /**/
  border-radius: 24px;
  margin-left: 10px;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.userInfo {
  cursor: pointer;
}
.homeItem {
  font-size: 30px;
  text-align: center;
  color: dodgerblue;
  font-family: ;
  padding-top: 250px;
}

/*  */
.el-menu {
  height: 100%;
}
/* tab */

.tabs {
  padding: 15px 15px 0 15px;
  .el-tag {
    margin-right: 15px;
  }
}
</style>
