                <el-dropdown-item @click.native="interview(scope.row.applicationId)"></el-dropdown-item>
                <el-dropdown-item @click.native="quit(scope.row.applicationId)"></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
        </el-table-column>-->
