import axios from 'axios'
//axios
/*https://www.kancloud.cn/yunye/axios/234845*/
let base = '/hr'

// post
function postRequest(url, params) {
    return axios({
        method: 'post',
        url: `${base}${url}`,
        data: params,
    })
}

//  baseURLpost
function postRequest0(url) {
    return axios({
        method: 'post',
        url: `${url}`
    })
}

// post
function postRequest1(url) {
    return axios({
        method: 'post',
        url: `${base}${url}`
    })
}

// get
function getRequest(url) {
    return axios({
        method: 'get',
        url: `${base}${url}`
    })
}

// get
function getRequest1(url, params) {
    let page = params.page
    let limit = params.limit
    return axios({
        method: 'get',
        url: `${base}${url}`,
        params: {
            page: page,
            // 6
            limit: limit
        }
    })
}

function getRequest2(url, params) {
    let page = params.page
    let limit = params.limit
    let state = params.state
    return axios({
        method: 'get',
        url: `${base}${url}`,
        params: {
            state: state,
            page: page,
            // 6
            limit: limit
        }
    })
}

function getRequest3(url, params) {
    let title = params
    return axios({
        method: 'get',
        url: `${base}${url}`,
        params: {
            title: title
        }
    })
}

// put
function putRequest(url) {
    return axios({
        method: 'put',
        url: `${base}${url}`,
    })
}

// put
function putRequest1(url, params) {
    return axios({
        method: 'put',
        url: `${base}${url}`,
        data: params
    })
}

export default {
    postRequest0,
    postRequest,
    postRequest1,
    getRequest,
    getRequest1,
    getRequest2,
    getRequest3,
    putRequest,
    putRequest1
}

